import { http } from '../utils/http';
import { handleResponse, handleError } from './ServiceUtils';

export const getCalendar = () => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'GET', `batches-service/meetings?meetingRange=MONTH`, true)
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
