import { http } from '../utils/http';

export const addCourse = (payload) => {
    return new Promise((resolve, reject) => {
        http(
            process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS,
            'POST',
            'subscriptions-service/courses',
            payload,
            true,
            { 'content-type': 'multipart/form-data', accept: '*/*' },
            {}
        )
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};

export const updateCourse = (payload, courseId) => {
    return new Promise((resolve, reject) => {
        http(
            process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS,
            'PUT',
            `subscriptions-service/courses/${courseId}`,
            payload,
            true,
            { 'content-type': 'multipart/form-data', accept: '*/*' },
            {}
        )
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
