import React, { useState, useEffect } from 'react';
import MainCard from '../../../ui-component/cards/MainCard';
import ConfirmDialog from '../../../ui-component/ConfirmDialog/ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { getOnlineCourses, getCourse, deleteCourse } from '../../../store/actions/CourseActions';
import { withStyles, useTheme } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip,
    Avatar
} from '@material-ui/core';

import { IconSearch } from '@tabler/icons';
import EditIcon from '@material-ui/icons/Edit';
import SubjectIcon from '@material-ui/icons/Subject';
import VisibilityIcon from '@material-ui/icons/Visibility';
import AddOnlineCourse from './add-course';
import useStyles from '../../ViewTableStyles';
import useUserRole from '../../../hooks/useUserRole';
import Calendar from '../../students/dashboard/Calendar';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function OnlineCourses() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'TEACHER', 'SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const history = useHistory();
    const theme = useTheme();

    const [openAddCourses, setOpenAddCourses] = useState(false);
    const [courseId, setCourseId] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const [confirmDialog, setConfirmDialog] = useState({
        isOpen: false,
        title: '',
        subTitle: ''
    });
    const [isSearchCourse, setSearchCourse] = useState('');
    const [isCourseUpdateData, setCourseUpdateData] = useState(null);

    const { allCourses } = useSelector((state) => state?.course);
    const newData = allCourses?.content;

    const totalCount = allCourses?.totalElements;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    const handleCourse = (id) => {
        setOpenAddCourses(true);
        setCourseUpdateData(id);
    };

    const handleViewModule = (id) => {
        setCourseId(id);
        dispatch(getCourse(id));
        history.push(`/admin/course/${id}/module`);
    };

    const handleSearchCourse = (event) => {
        setSearchCourse(event.target.value);
    };

    const handleFetchCourses = () => {
        dispatch(getOnlineCourses(isSearchCourse ? isSearchCourse : '', page + 1, rowsPerPage));
    };
    const handleDeleteCourse = (id) => {
        const payload = {
            status: 'INACTIVE'
        };
        dispatch(
            deleteCourse(payload, id, () => {
                dispatch(getOnlineCourses(isSearchCourse ? isSearchCourse : '', page + 1, rowsPerPage));
            })
        );
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        });
    };

    useEffect(() => {
        handleFetchCourses(isSearchCourse ? isSearchCourse : '', page + 1, rowsPerPage);
    }, [isSearchCourse, page, rowsPerPage]);

    return (
        <>
            {loggedUserProfile?.role === 'TEACHER' && <Calendar />}
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                        <span className={classes.pageTitle}>&nbsp; All Courses </span>
                    )}

                    {loggedUserProfile?.role === 'TEACHER' && <span className={classes.pageTitle}>&nbsp; Your Courses </span>}
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3} style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Grid item xs={12} sm={6} md={6}>
                            <OutlinedInput
                                className={classes.searchControl}
                                id="input-search-header"
                                onChange={(event) => handleSearchCourse(event)}
                                placeholder="Search"
                                startAdornment={
                                    <InputAdornment position="start">
                                        <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                    </InputAdornment>
                                }
                            />
                        </Grid>
                        {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                            <Grid item xs={12} sm={6} md={6}>
                                <div className={classes.btn}>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        onClick={() => {
                                            setOpenAddCourses(true);
                                            setCourseUpdateData(null);
                                        }}
                                        className={classes.btnAdd}
                                    >
                                        Add Course
                                    </Button>
                                </div>
                            </Grid>
                        )}
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell align="center"> Image </TableCell>
                                    <TableCell> Title </TableCell>
                                    <TableCell> Description </TableCell>
                                    <TableCell align="center"> Age Group </TableCell>
                                    <TableCell align="center"> Status </TableCell>

                                    <TableCell align="center"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!allCourses ? (
                                <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {newData?.map((row, index) => (
                                        <StyledTableRow key={index}>
                                            <TableCell align="center">
                                                {/* {row?.imageUrl} */}
                                                <img src={row?.imageUrl} width="40px" height="40px" />
                                            </TableCell>
                                            <TableCell> {row?.title} </TableCell>
                                            <TableCell> {row?.description} </TableCell>

                                            <TableCell align="center">
                                                {row?.minAge}-{row?.maxAge}
                                            </TableCell>

                                            <TableCell align="center">
                                                <Typography
                                                    className={classes.status}
                                                    style={{
                                                        backgroundColor:
                                                            (row?.status === 'ACTIVE' && theme.palette.success.main) ||
                                                            (row?.status === 'PENDING' && 'blue') ||
                                                            (row?.status === 'INACTIVE' && theme.palette.warning.dark)
                                                    }}
                                                >
                                                    {row?.status}
                                                </Typography>
                                            </TableCell>

                                            <TableCell align="center">
                                                {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                                                    <Tooltip title="Update Course">
                                                        <IconButton onClick={() => handleCourse(row)}>
                                                            <EditIcon color="success" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                )}
                                                <Tooltip title="View Modules">
                                                    <IconButton onClick={() => handleViewModule(row?.id)}>
                                                        <VisibilityIcon color="primary" fontSize="small" style={{ cursor: 'pointer' }} />
                                                    </IconButton>
                                                </Tooltip>
                                                {loggedUserProfile?.role === 'SUPER_ADMIN' && (
                                                    <Tooltip title="Delete Course">
                                                        <IconButton
                                                            onClick={() => {
                                                                setConfirmDialog({
                                                                    isOpen: true,
                                                                    title: 'Delete Course',
                                                                    subTitle: 'Are you sure you want to delete this course ?',
                                                                    onConfirm: () => {
                                                                        handleDeleteCourse(row?.id);
                                                                    }
                                                                });
                                                            }}
                                                        >
                                                            <DeleteForeverIcon color="error" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                )}
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={totalCount || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
                {openAddCourses && (
                    <AddOnlineCourse
                        isOpen={openAddCourses}
                        setOpen={setOpenAddCourses}
                        isEditData={isCourseUpdateData}
                        handleFetchCourses={handleFetchCourses}
                    />
                )}
                {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}
            </MainCard>
        </>
    );
}
