import React, { useEffect, useState, useRef } from 'react';
import styled from '@emotion/styled';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';
import { useParams } from 'react-router-dom';
import { Grid, useMediaQuery, useTheme } from '@mui/material';
import calendarStyle from './Calendar.style';
import { getCalendar } from '../../../services/Event';

const FullCalendarWrapper = styled.div`
    ${calendarStyle}
`;

function EmptyCard() {
    const theme = useTheme();
    const matchesSM = useMediaQuery(theme.breakpoints.down('sm'));
    const calendar = useRef(null);
    const [calendarEvents, setCalendarEvents] = useState([]);
    const { } = useParams();

    const GetCalendar = async () => {
        await getCalendar()
            .then((res) => {
                const events = mapEventsToCalendarEvents(res);
                setCalendarEvents(events);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        GetCalendar(calendarEvents);
    }, []);

    const mapEventsToCalendarEvents = (calendarEvents) => {
        let mappedData = [];
        calendarEvents.map((element) => {
            let ev = {};
            ev = { ...ev, title: element?.agenda, start: element?.startDateTime, end: element.endDateTime, url: element?.joinUrl };
            mappedData.push(ev);
        });

        return mappedData;
    };

    return (
        <FullCalendarWrapper>
            <FullCalendar
                ref={calendar}
                initialView="dayGridMonth"
                plugins={[dayGridPlugin, interactionPlugin]}
                events={calendarEvents}
                selectable="true"
                selectHelper="true"
                editable={true}
                height={'50vh'}
                headerToolbar={{
                    left: 'prev,next',
                    center: 'title',
                    right: 'dayGridDay,dayGridWeek,dayGridMonth'
                }}
            />
        </FullCalendarWrapper>
    );
}

function Calendar() {
    return (
        <React.Fragment>
            <Grid container spacing={6}>
                <Grid item xs={12}>
                    <EmptyCard />
                </Grid>
            </Grid>
        </React.Fragment>
    );
}

export default Calendar;
