import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import * as yup from 'yup';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, Grid, Link, Divider } from '@material-ui/core';
import { getOnlineCourses } from '../../../../store/actions/CourseActions';
import { FormProvider, RHFTextField, RHFSelect } from '../../../../components/hook-form';
import CloseIcon from '@material-ui/icons/Close';
import useStyles from '../../../DialogStyles';
import { IconButton } from '@mui/material';
import { addCourse, updateCourse } from '../../../../services/CourseServices';
import { useParams } from 'react-router-dom';
import { setMessage } from '../../../../store/actions/MessageActions';
import PerfectScrollbar from 'react-perfect-scrollbar';

const STATUS_ITEMS = [
    { id: 0, status: 'ACTIVE' },
    { id: 1, status: 'INACTIVE' }
];

const MIN_AGE = [
    // { id: 0, status: '1' },
    // { id: 1, status: '2' },
    { id: 2, status: '3' },
    { id: 3, status: '4' },
    { id: 4, status: '5' },
    { id: 5, status: '6' },
    { id: 6, status: '7' },
    { id: 7, status: '8' },
    { id: 8, status: '9' },
    { id: 9, status: '10' },
    { id: 10, status: '11' },
    { id: 11, status: '12' },
    { id: 12, status: '13' },
    { id: 13, status: '14' },
    { id: 14, status: '15' }
];

const MAX_AGE = [
    // { id: 0, status: '1' },
    // { id: 1, status: '2' },
    { id: 2, status: '3' },
    { id: 3, status: '4' },
    { id: 4, status: '5' },
    { id: 5, status: '6' },
    { id: 6, status: '7' },
    { id: 7, status: '8' },
    { id: 8, status: '9' },
    { id: 9, status: '10' },
    { id: 10, status: '11' },
    { id: 11, status: '12' },
    { id: 12, status: '13' },
    { id: 13, status: '14' },
    { id: 14, status: '15' }
];

const validationSchema = yup.object({
    title: yup.string('Enter Title').required('Title is required').trim(),
    description: yup.string().optional().trim(),
    minAge: yup.number().min(3).max(15).required('Min age is required'),
    maxAge: yup.number().moreThan(yup.ref('minAge')).required('Max age is required'),
    // maxAge: yup.number().nullable().moreThan(yup.ref('minAge')).max(15).required('Max age is required'),
    status: yup.string('Choose Status').required('Status is required')
});

export default function AddOnlineCourse(props) {
    const [file, setFile] = useState(null);
    const { isOpen, setOpen, isEditData, handleFetchCourses } = props;
    const [displayImage, setDisplayImage] = useState();
    const { id } = useParams();
    const classes = useStyles();
    const dispatch = useDispatch();
    const location = useLocation();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const initialValues = {
        title: isEditData?.title || '',
        description: isEditData?.description || '',
        minAge: isEditData?.minAge || 3,
        maxAge: isEditData?.maxAge || 15,
        status: isEditData?.status || 'ACTIVE'
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });

    const { reset, handleSubmit } = methods;

    const handlePopupClose = () => {
        setOpen(false);
        reset();
    };
    const handleChange = function loadFile(event) {
        setDisplayImage(URL.createObjectURL(event?.target?.files[0]));
        setFile(event?.target?.files[0]);
    };

    const onSubmit = (data) => {
        const payload = {
            title: data?.title,
            description: data?.description,
            minAge: data?.minAge,
            maxAge: data?.maxAge,
            status: data?.status
        };

        const formData = new FormData();

        if (file?.size > 0) formData.append('image', file);
        if (isEditData) formData.append('courseBody ', JSON.stringify(payload));
        else formData.append('courseBody ', JSON.stringify(payload));

        if (isEditData) {
            updateCourse(formData, isEditData?.id)
                .then((data) => {
                    dispatch(setMessage(data?.message ? data?.message : 'Course Updated Successfully'));
                    handleFetchCourses();
                    // To clear cache of page
                    if ('caches' in window) {
                        caches.keys().then((names) => {
                            // Delete all the cache files
                            names.forEach((name) => {
                                caches.delete(name);
                            });
                        });
                        window.location.reload(true);
                    }
                })
                .catch((error) => {
                    console.log(error);
                    dispatch(setMessage(error?.data?.message ? error?.data?.message : 'Something Went Wrong'));
                });
            handlePopupClose();
        } else {
            addCourse(formData)
                .then((data) => {
                    dispatch(setMessage(data?.message ? data?.message : 'Course Added Successfully'));
                    dispatch(getOnlineCourses());
                })
                .catch((error) => {
                    dispatch(setMessage(error?.data?.message ? error?.data?.message : 'Something Went Wrong'));
                });
            handlePopupClose();
        }
    };

    useEffect(() => {
        dispatch(getOnlineCourses());
    }, []);

    useEffect(() => {
        if (isEditData) {
            setDisplayImage(isEditData?.imageUrl);

            fetch(isEditData?.imageUrl, { mode: 'no-cors' }).then(async (response) => {
                const contentType = response.headers.get('content-type');
                const blob = await response.blob();
                const file = new File([blob], 'updatedImage.jpg', { contentType });
                //setFile(file);
            });
        }
    }, []);

    return (
        <div>
            <Dialog open={isOpen} classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}>{isEditData ? 'Update Course' : 'Add Course'}</Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <PerfectScrollbar className={classes.ScrollHeight}>
                        <DialogContent>
                            <div>
                                <label className={classes.label}>
                                    Course Image <span className={classes.mandatory}>*</span>
                                </label>
                                <div>
                                    <label htmlFor="upload">
                                        <IconButton color="secondary" aria-label="upload picture" component="span">
                                            <img
                                                id="ImageCover"
                                                src={displayImage}
                                                className={classes.img}
                                                style={{ height: '200px', width: '200px' }}
                                            />
                                        </IconButton>
                                    </label>
                                    <label htmlFor="bookCover" />
                                </div>
                                <div>
                                    {isEditData ? (
                                        <input
                                            type="file"
                                            onChange={handleChange}
                                            id="upload"
                                            name="image"
                                            accept="image/*"
                                            className={classes.imageInput}
                                        />
                                    ) : (
                                        <input
                                            type="file"
                                            onChange={handleChange}
                                            id="upload"
                                            name="image"
                                            accept="image/*"
                                            className={classes.imageInput}
                                            required
                                        />
                                    )}
                                </div>
                            </div>
                            <Grid container>
                                <Grid item xs={12}>
                                    <label>
                                        Course Title <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField name="title" placeholder="Title" />
                                    <br /> <br />
                                    <label> Course Description </label>
                                    <RHFTextField name="description" placeholder="Description" />
                                    <br /> <br />
                                    <label>
                                        Min Age <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFSelect name="minAge" label="">
                                        <option value="" disabled>
                                            Select minimum age
                                        </option>
                                        {MIN_AGE?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                    <br /> <br />
                                    <label>
                                        Max Age <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFSelect name="maxAge" label="">
                                        <option value="" disabled>
                                            Select maximum age
                                        </option>
                                        {MAX_AGE?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                    <br /> <br />
                                    <label>
                                        Status <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFSelect name="status" label="">
                                        {STATUS_ITEMS?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                  
                                </Grid>
                            </Grid>
                        </DialogContent>
                    </PerfectScrollbar>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="secondary" className={classes.buttonCancel} onClick={handlePopupClose}>
                            Close
                        </Button>
                        <Button variant="contained" color="secondary" type="submit" className={classes.buttonSubmit}>
                            {isEditData ? 'Update' : 'Add'}
                        </Button>
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </div>
    );
}
